import React, { Component } from 'react';
import { Compare } from "../components/Compare";
import { CompareVer2 } from "../components/CompareVer2";
import { Account } from "../components/Account";
import { MultiSelectComponent } from "../components/MultiSelectComponent";
import { Table, Thead, Tbody, Tr, Th } from 'react-super-responsive-table';
import 'react-super-responsive-table/dist/SuperResponsiveTableStyle.css';
import { CheckBox } from '../components/CheckBox';

const pako = require('pako');

function decompressAndDeserializeObject(compressedBytes) {
    // Decompress the data using pako
    const decompressedData = pako.inflate(new Uint8Array(compressedBytes), { to: 'string' });

    // Parse the JSON to create the object
    const dataObject = JSON.parse(decompressedData);

    return dataObject;
}

const exchanges = [
  { symbol: "binance", on: true, isBuy: true },
  { symbol: "bybit", on: true, isBuy: false },
  { symbol: "coinex", on: true, isBuy: false },
  { symbol: "huobi", on: true, isBuy: false },
  { symbol: "kucoin", on: true, isBuy: false },
  { symbol: "mexc", on: true, isBuy: false },
  { symbol: "poloniex", on: true, isBuy: false },
  { symbol: "bitget", on: true, isBuy: false },
];

export class Symbols extends Component {

  constructor(props) {
    super(props);
    let min = sessionStorage.getItem("min");
    let max = sessionStorage.getItem("max") === null ? "50" : sessionStorage.getItem("max");
    let isAutoSettings = sessionStorage.getItem("isAutoSettings");
    let networkFilter = sessionStorage.getItem("networkFilter");

    this.state = {
      dataTime: "", value: [], networks: [], min: Number(min), max: Number(max)
      , isVersion2: false
      , money: "-1"
      , mainClass: ""
      , isShowAccount: false
      , isAutoSettings: !(isAutoSettings !== null && isAutoSettings === "false")
      , networkFilter: (networkFilter !== null && networkFilter === "true")
    };
    this.changeMin = this.changeMin.bind(this);
    this.changeMax = this.changeMax.bind(this);
    this.checkExchange = this.checkExchange.bind(this);
    this.exit = this.exit.bind(this);
    this.account = this.account.bind(this);

    this.reset = this.reset.bind(this);
    this.changeIsAutoSettings = this.changeIsAutoSettings.bind(this);
    this.checkExchangeFilter = this.checkExchangeFilter.bind(this);
    this.checkSymbolFilter = this.checkSymbolFilter.bind(this);
    this.addNetwork = this.addNetwork.bind(this);
    this.filterNetworks = this.filterNetworks.bind(this);
    this.changeNetworkFilter = this.changeNetworkFilter.bind(this);
    this.changeMoney = this.changeMoney.bind(this);
    this.changeVersion = this.changeVersion.bind(this);
    this.maxProfit = this.maxProfit.bind(this);
    this.filterVolume = this.filterVolume.bind(this);
    this.checkExchangeFilterVer2 = this.checkExchangeFilterVer2.bind(this);
    this.checkExchangeFilterSellVer2 = this.checkExchangeFilterSellVer2.bind(this);
    this.loading = this.loading.bind(this);
  }

  componentDidMount() {
    this.loadData();
  }

  loading(data, i, socket){
    // let data = JSON.parse(event.data);
      let array = JSON.parse(data.Compares);
      // console.log(array);
      if(this.state.isVersion2){
        let filter = array.filter(symbol=> symbol.Groups.length > 0);

        if(this.state.money === "-1"){
          filter = filter.filter(symbol=>  this.filterVolume(symbol.Groups, 4, 0, 100000000));
        }
        else if(this.state.money === "0"){
          filter = filter.filter(symbol=> this.filterVolume(symbol.Groups, 0, 0, 100));
        }
        else if(this.state.money === "1"){
          filter = filter.filter(symbol=>  this.filterVolume(symbol.Groups, 1, 100, 200));
        }
        else if(this.state.money === "2"){
          filter = filter.filter(symbol=>  this.filterVolume(symbol.Groups, 2, 200, 500));
        }
        else if(this.state.money === "3"){
          filter = filter.filter(symbol=>  this.filterVolume(symbol.Groups, 3, 500, 1000));
        }
        else if(this.state.money === "4"){
          filter = filter.filter(symbol=>  this.filterVolume(symbol.Groups, 4, 1000, 100000000));
        }
        filter.forEach(symbol=>{
          let indexMoney = this.state.money;
          if (indexMoney === "-1") indexMoney = "4";
          let spread = 0;
          if(symbol.Groups.length > 0){
            let group = this.maxProfit(symbol.Groups, indexMoney);
            if(group !== null){
              spread = group.Spread;
            }
            // spread = Math.max.apply(Math, symbol.Groups.map(function(o) { return o.MaxSpread; }));
          }
          symbol.Spread = spread;
        });
        let filter2 = filter.filter(symbol=> symbol.Spread > this.state.min && symbol.Spread < this.state.max);
        filter2.sort((a,b) => b.Spread - a.Spread);
        if(this.state.isAutoSettings){
          this.setState({ value: filter2});
        }
        else{
          let filter3 = filter2.filter(symbol=> this.checkExchangeFilterVer2(symbol));
          this.setState({ value: filter3});
        }
      }
      else{
        if(this.state.isAutoSettings){
          let symbols = array.filter(symbol=> symbol.Spread > this.state.min && symbol.Spread < this.state.max && this.checkExchange(symbol.Symbols));
          symbols.sort((a,b) => b.Spread - a.Spread);
          this.setState({ value: symbols});
        }
        else{
          let filter1 = this.checkExchangeFilter(array);
          let filter2 = filter1.filter(symbol=> this.checkSymbolFilter(symbol));
          let filter3 = filter2.filter(symbol=> symbol.Spread > this.state.min && symbol.Spread < this.state.max);
          filter3.sort((a,b) => b.Spread - a.Spread);
          this.setState({ value: filter3});
        }
      }

      let dataNetworks = JSON.parse(data.Networks);

      let networks = dataNetworks.map(n=>this.addNetwork(n));
      this.setState({ networks: networks});
      if(i % 3 === 0){
        socket.send("Send!");
      }
  }

  changeVersion(event) {
    this.setState({ isVersion2: event.target.checked });
  }

  account() {
    this.setState({ isShowAccount: !this.state.isShowAccount });
    if (this.state.isShowAccount) {
      this.setState({ mainClass: "" });
    }
    else {
      this.setState({ mainClass: "main" });
    }
  }

  changeMoney(event) {
    this.setState({ money: event.target.value });
  }

  changeNetworkFilter(event) {
    this.setState({ networkFilter: event.target.checked });
    sessionStorage.setItem("networkFilter", event.target.checked);
  }

  changeIsAutoSettings(event) {
    this.setState({ isAutoSettings: event.target.checked });
    sessionStorage.setItem("isAutoSettings", event.target.checked);
  }
  exit() {
    sessionStorage.setItem("accessToken", "");
    sessionStorage.setItem("isLogin", "false");
    window.location.href = "/";
  }
  changeMin(event) {
    this.setState({ min: event.target.value });
    sessionStorage.setItem("min", event.target.value);
  }
  changeMax(event) {
    this.setState({ max: event.target.value });
    sessionStorage.setItem("max", event.target.value);
  }

  reset() {
    exchanges.forEach((exchange) => {
      exchange.isBuy = false;
      sessionStorage.setItem(exchange.symbol + "IsBuy", false);
    });
  }

  filterNetworks(symbol) {
    let i = 0;
    if (symbol.AssetInfo !== null && symbol.AssetInfo.Networks !== null) {
      let networks = JSON.parse(sessionStorage.getItem("networks"));
      // console.log(networks);
      if (networks !== null && networks.length > 0) {

        symbol.AssetInfo.Networks.forEach(n => {
          if (networks.includes(n.Name) === true) i++;
        });
      }
    }
    if (i > 0) return true;
    else return false;
  }

  checkExchange(symbols) {
    let buy = symbols.find(symbol => symbol.IsBuy === true);
    let find = exchanges.find(exchange => exchange.symbol === buy.Exchange);
    if (find.on) {
      if (this.state.networkFilter) return this.filterNetworks(buy);
      else return true;
    }
    else return false;
  }

  checkExchangeFilter(array) {
    array.forEach(item => {
      item.Symbols.forEach(symbol => {
        let find = exchanges.find(exchange => exchange.symbol === symbol.Exchange);
        if (find.on) {
          symbol.IsBuy = find.isBuy;
        }
        else {
          symbol.IsBuy = null;
        }
      });
    });
    return array;
  }
  checkExchangeFilterSellVer2(group) {
    let find = exchanges.find(exchange => exchange.symbol === group.Exchange2);
    if (find.on) return true;
    else return false;
  }

  checkExchangeFilterVer2(symbol) {
    let findExchange = exchanges.find(exchange => exchange.isBuy === true);
    if (findExchange === undefined) return false;
    else {
      if (symbol.Groups.length > 0) {
        let find = symbol.Groups.find(group => group.Exchange1 === findExchange.symbol);
        if (find === undefined) return false;
        else {
          if (this.checkExchangeFilterSellVer2(find)) return true;
          else return false;
        }
      }
      else return false;
    }
  }

  checkSymbolFilter(item) {
    let buy = item.Symbols.find(item => item.IsBuy === true);
    let arraySell = item.Symbols.filter(item => item.IsBuy === false);
    if (buy != null && arraySell != null && arraySell.length > 0) {
      let sell = arraySell[0];
      if (arraySell.length > 1) {
        arraySell.forEach(symbol => {
          if (sell.BestBidPrice < symbol.BestBidPrice) {
            sell = symbol;
          }
        });
        arraySell.forEach(symbol => {
          if (symbol !== sell) {
            symbol.IsBuy = null;
          }
        });
      }
      item.Spread = (sell.BestBidPrice - buy.BestAskPrice) / buy.BestAskPrice * 100;
      if (this.state.networkFilter) return this.filterNetworks(buy);
      else return true;
    }
    else return false;
  }

  addNetwork(name) {
    return { value: name, label: name };
  }


  maxProfit(arr, money) {
    let maxProfit = -1;
    let maxObj = null;

    arr.forEach(element => {
      if (element.Data.length > 0) {
        let val = element.Data[money];
        if (val.Profit !== undefined) {
          if (val.Profit > maxProfit) {
            maxProfit = val.Profit;
            maxObj = val;
          }
        }
      }
    });
    return maxObj;
  }

  filterVolume(groups, index, min, max) {
    let find = groups.find(g => g.Data.length > 0 && g.Data[index].Volume !== undefined && g.Data[index].Volume >= min && g.Data[index].Volume <= max);
    if (find === undefined) return false;
    else return true;
  }


  static renderStatisticsTable(array) {
    return (
      <Table className='table table-dark table-hover text-center align-middle mt-3'>
        <Thead>
          <Tr>
            <Th className="bg-gradient" style={{ width: "20%" }}>Name</Th>
            <Th className="bg-gradient" style={{ width: "20%" }}>Spread</Th>
            <Th className="bg-gradient" style={{ width: "20%" }}>Buy</Th>
            <Th className="bg-gradient" style={{ width: "20%" }}>Sell</Th>
            <Th className="bg-gradient" style={{ width: "20%" }}>Update</Th>
          </Tr>
        </Thead>
        <Tbody>
          {array.map((compare, index) =>
            <Compare key={compare.Name} compare={compare} />
          )}
        </Tbody>
      </Table>
    );
  }


  static renderStatisticsTableVer2(array, money) {
    let indexMoney = money;
    if (indexMoney === "-1") indexMoney = 4;
    return (
      <Table className='table table-dark table-hover text-center align-middle mt-3'>
        <Thead>
          <Tr>
            <Th className="bg-gradient" style={{ width: "20%" }}>Name</Th>
            <Th className="bg-gradient" style={{ width: "20%" }}>Max spread (%)</Th>
            <Th className="bg-gradient" style={{ width: "20%" }}>Max volume ($)</Th>
            <Th className="bg-gradient" style={{ width: "20%" }}>Max profit ($)</Th>
            <Th className="bg-gradient" style={{ width: "20%" }}>Update</Th>
          </Tr>
        </Thead>
        <Tbody>
          {array.map((compare, index) =>
            <CompareVer2 key={compare.Name} compare={compare} money={indexMoney} />
          )}
        </Tbody>
      </Table>
    );
  }

  render() {
    return (
      <div className='container mt-3'>
        {this.state.isShowAccount ? (<Account account={this.account} />) : (<></>)}
        <div className={this.state.mainClass}>
          <div className='d-flex flex-column'>
            <div className='d-flex justify-content-end'>
              <button className='btn btn-outline-secondary mx-3' onClick={this.account}>Account</button>
              <button type="button" className="btn btn-outline-danger" onClick={this.exit}>Log Out</button>
            </div>
            <div className='d-flex justify-content-center'>
              <div className="form-check form-switch m-3">
                <input className="form-check-input" type="checkbox" checked={this.state.isVersion2} onChange={this.changeVersion}></input>
                <label className="form-check-label text-white">{this.state.isVersion2 ? (<>Version: 2.0</>) : (<>Version: 1.0</>)}</label>
              </div>
              <div className="input-group input-group-sm m-3 w-auto">
                <span className="input-group-text bg-gradient bg-dark text-white">Min:</span>
                <input type="number" className="form-control" value={this.state.min} onChange={this.changeMin} />
              </div>
              <div className="input-group input-group-sm m-3 w-auto">
                <span className="input-group-text bg-gradient bg-dark text-white">Max:</span>
                <input type="number" className="form-control" value={this.state.max} onChange={this.changeMax} />
              </div>
            </div>
            <div className='d-flex flex-wrap justify-content-center'>

              <div className='d-flex justify-content-center'>
                <div className='m-3'>
                  <input type="checkbox" className="btn-check" id="btn-check-outlined-is-auto-settings" checked={this.state.isAutoSettings} onChange={this.changeIsAutoSettings}></input>
                  <label className="btn btn-outline-secondary p-4" htmlFor="btn-check-outlined-is-auto-settings">Auto</label>
                </div>
              </div>

              {exchanges.map((exchange, index) => {
                return (<CheckBox key={index} exchange={exchange} isAutoSettings={this.state.isAutoSettings} reset={this.reset} />)
              })}

              {this.state.isVersion2 ?
                (<></>)
                :
                (<div className=' d-flex flex-column align-items-center'>
                  <MultiSelectComponent disabled={this.state.networkFilter} list={this.state.networks} />
                  <div className="form-check form-switch mt-1">
                    <input className="form-check-input" type="checkbox" checked={this.state.networkFilter} onChange={this.changeNetworkFilter}></input>
                    <label className="form-check-label text-white">Network filter</label>
                  </div>
                </div>)}

            </div>
            {this.state.isVersion2 ?
              (<div className='d-flex flex-wrap justify-content-center'>
                <div className=' d-flex flex-column align-items-center'>
                  <select className="m-3 form-select bg-dark text-secondary" aria-label="Default select example" value={this.state.money} onChange={this.changeMoney} >
                    <option value="-1">Maximum</option>
                    <option value="0">0-100 $</option>
                    <option value="1">100-200 $</option>
                    <option value="2">200-500 $</option>
                    <option value="3">500-1000 $</option>
                    <option value="4">1000+ $</option>
                  </select>
                </div>
              </div>)
              :
              (<></>)}

          </div>
          {this.state.isVersion2 ? (<>{Symbols.renderStatisticsTableVer2(this.state.value, this.state.money)}</>) : (<>{Symbols.renderStatisticsTable(this.state.value)}</>)}
          {/* {Symbols.renderStatisticsTable(this.state.value)} */}
        </div>
      </div>

    );
  }

  async loadData() {

    let token = sessionStorage.getItem("accessToken");
    let socket = new WebSocket(`wss://${process.env.REACT_APP_API}/api/WebSockets/zip`, ["client", token]);
    let i = 0;
    socket.addEventListener('message', (event) => {
      // console.log(event.data);
      if (event.data instanceof Blob) {
        try{
          const reader = new FileReader();
          let main = this;
          reader.onload = () =>{
              const decompressedObject = decompressAndDeserializeObject(reader.result);
              // console.log('Decompressed Object:', decompressedObject);
              i++;
              main.loading(decompressedObject, i, socket);
          };
          reader.readAsArrayBuffer(event.data);
        }
        catch{

        }
    }
    });
    socket.onclose = function (e) {
      console.log("WebSocket Error: ", e);
      if (e.code !== 1008) {
        sessionStorage.setItem("accessToken", "");
        sessionStorage.setItem("isLogin", "false");
        window.location.href = "/";
      }
    };
  }
}
