import React, { Component } from 'react';

export class CheckBox extends Component {
    constructor(props) {
        super(props);
        this.change = this.change.bind(this);
        this.changeIsBuy = this.changeIsBuy.bind(this);
        let value = sessionStorage.getItem(props.exchange.symbol);
        let isBuy = sessionStorage.getItem(props.exchange.symbol + "IsBuy");
        props.exchange.on = !(value !== null && value === "false")
        props.exchange.isBuy = props.exchange.symbol === "binance"?(!(isBuy !== null && isBuy === "false")):(isBuy !== null && isBuy === "true");
    }

    change(event) {
        this.props.exchange.on = event.target.checked;
        sessionStorage.setItem(this.props.exchange.symbol, event.target.checked);
    }

    changeIsBuy(event) {
        if (event.target.checked) this.props.reset();
        this.props.exchange.isBuy = event.target.checked;
        sessionStorage.setItem(this.props.exchange.symbol + "IsBuy", event.target.checked);
    }

    render() {
        return (
            <div className='d-flex flex-column'>
                <div className='m-3'>
                    <input type="checkbox" className="btn-check" id={"btn-check-outlined-" + this.props.exchange.symbol} checked={this.props.exchange.on} onChange={this.change} />
                    <label className="btn btn-outline-secondary" htmlFor={"btn-check-outlined-" + this.props.exchange.symbol}>{this.props.exchange.symbol}</label>
                </div>
                <div className="form-check form-switch mx-4">
                    <input className="form-check-input" type="checkbox" disabled={!this.props.exchange.on || this.props.isAutoSettings} checked={this.props.exchange.isBuy} onChange={this.changeIsBuy}></input>
                    <label className="form-check-label text-white">{this.props.exchange.isBuy ? (<>Buy</>) : (<>Sell</>)}</label>
                </div>
            </div>
        );
    }
}