import React, { Component } from 'react';
import telegram from '../telegram.png';

export class Account extends Component {

  constructor(props) {
    super(props);
    this.state = { trialKey: "", minSpread: 0, userName: "", step: 0, isStep: false, time: new Date().toUTCString() };
    this.changeTrialKey = this.changeTrialKey.bind(this);
    this.saveTrialKey = this.saveTrialKey.bind(this);
    this.changeMinSpread = this.changeMinSpread.bind(this);
    this.saveTelegramSettings = this.saveTelegramSettings.bind(this);
    this.changeUserName = this.changeUserName.bind(this);
    this.changeStep = this.changeStep.bind(this);
    this.changeIsStep = this.changeIsStep.bind(this);
  }

  componentDidMount() {
    this.loadData();
  }
  
  changeTrialKey(event) {
    this.setState({trialKey: event.target.value});
  }

  changeMinSpread(event) {
    this.setState({minSpread: event.target.value});
  }

  changeUserName(event) {
    this.setState({userName: event.target.value});
  }

  changeStep(event) {
    this.setState({step: event.target.value});
  }
  changeIsStep(event) {
    this.setState({isStep: event.target.checked});
  }

  async saveTelegramSettings() {
    let token = sessionStorage.getItem("accessToken");
    let data = {
      method: 'POST',
      headers: { 
        "Accept": "application/json",
        'Content-Type': 'application/json',
        "Authorization": "Bearer " + token
      },
      body: JSON.stringify({
        userId: "",
        chatId: 0,
        endTime: this.state.time,
        isRun: false,
        isStep: this.state.isStep,
        minSpread: this.state.minSpread,
        step: this.state.step,
        name: this.state.userName
        })
      };
    let response = await fetch(`https://${process.env.REACT_APP_API}/api/WebSockets/setTelegramUser`, data);
    if(response.status === 200){
      // let result = await response.text();
      // console.log(result);
      window.location.href = "/";
    }
    else{
      let result = await response.text();
      alert(result);
    }
  }

  async saveTrialKey(){
    if(this.state.trialKey !== ""){
        let token = sessionStorage.getItem("accessToken");
        let data = {
            method: 'GET',
            headers: { 
            "Accept": "application/json",
            'Content-Type': 'application/json',
            "Authorization": "Bearer " + token
        }};
        let response = await fetch(`https://${process.env.REACT_APP_API}/spread/set?code=` + this.state.trialKey, data);
        if(response.status === 200){
            let result = await response.json();
            this.setState({time: result});
        }
        else{
            let result = await response.json();
            alert(result);
        }
    }
    this.setState({trialKey: ""});
  }

  render() {
    var date1 =  Date.parse(new Date().toUTCString());
    var date2 =  Date.parse(new Date(this.state.time).toString());
    var offset = new Date().getTimezoneOffset();
    var diff = date2 - (date1 + (offset * 60000));

    let isTrial = true;
    if(diff < 0) isTrial = false;
    return (
        <div className="fixed-top vh-100">
          <div className='d-flex h-100 justify-content-center align-items-center'>
            <div className='border border-secondary bg-gradient login1'>
              <div className='border border-secondary bg-gradient login2'>
                <div className='border border-secondary bg-dark login3'>
                  <div className='ratio ratio-1x1' style={{width:"320px"}}>
                    <div className='p-4 d-flex flex-column align-items-center'>
                        <div className='d-flex w-100 justify-content-end'>
                          <button className="btn btn-outline-light btn-close bg-light" onClick={this.props.account}></button>
                        </div>
                        <div className='d-flex w-75'>
                            <small className='text-white me-1'>
                                End:
                            </small>
                            {isTrial?(<>
                                <small className='text-success'>
                                    {new Date(this.state.time).toLocaleString()} UTC
                                </small>
                            </>):(<>
                                <small className=' text-danger'>
                                    {new Date(this.state.time).toLocaleString()} UTC
                                </small>
                            </>)}
                        </div>
                        <div className='d-flex w-75'>
                          <div className="input-group input-group-sm m-1">
                            <span className="input-group-text bg-gradient bg-dark text-white">Trial key:</span>
                            <input className="form-control" value={this.state.trialKey} onChange={this.changeTrialKey}/>
                          </div>
                          <button className='btn btn-outline-secondary btn-sm m-1' onClick={this.saveTrialKey}>Use</button>
                        </div>
                        <small className='text-white mt-1 w-75'>
                          Telegram settings:
                        </small>
                        <div className="input-group input-group-sm m-1 w-75">
                          <span className="input-group-text bg-gradient bg-dark text-white">User name:</span>
                          <input className="form-control" value={this.state.userName} onChange={this.changeUserName}/>
                        </div>
                        <div className="input-group input-group-sm m-1 w-75">
                          <span className="input-group-text bg-gradient bg-dark text-white">Min spread:</span>
                          <input type='number' className="form-control" value={this.state.minSpread} onChange={this.changeMinSpread}/>
                        </div>
                        <div className="input-group input-group-sm m-1 w-75">
                          <div className="input-group-text bg-gradient bg-dark text-white">
                            <input className="form-check-input me-2" type="checkbox" checked={this.state.isStep} onChange={this.changeIsStep}/>Step:
                          </div>
                          <input type='number' className="form-control" value={this.state.step} onChange={this.changeStep}/>
                        </div>
                        <div className='d-flex m-1'>
                          <button className='btn btn-outline-secondary btn-sm me-2' onClick={this.saveTelegramSettings}>Save telegram settings</button>
                          <a href='https://t.me/SearchSpreadBot' target="_blank" rel="noreferrer">
                            <div className='d-telegram m-auto'>
                              <img className='m-1' src={telegram} alt='telegram'></img>
                            </div>
                          </a>
                        </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
    );
  }
  
  async loadData(){
    let token = sessionStorage.getItem("accessToken");
    let data = {
        method: 'GET',
        headers: { 
        "Accept": "application/json",
        'Content-Type': 'application/json',
        "Authorization": "Bearer " + token
      }};
      let response = await fetch(`https://${process.env.REACT_APP_API}/spread/get`, data);
      if(response.status === 200){
        let result = await response.json();
        this.setState({time: result.endTime, minSpread: result.minSpread, userName: result.name === null? "" : result.name, step: result.step, isStep: result.isStep });
      }
      else{
        let result = await response.json();
        alert(result);
      }
  }

}