import React, { Component } from 'react';
import { SymbolVer2 } from "./SymbolVer2";
import { Tr, Td } from 'react-super-responsive-table';
import 'react-super-responsive-table/dist/SuperResponsiveTableStyle.css';
import { SymbolVer3 } from './SymbolVer3';

// const maxObj = arr.reduce((accumulator, current) => {
//   return accumulator.id > current.id ? accumulator : current;
// });

export class CompareVer2 extends Component {

  constructor(props) {
    super(props);
    this.state = { compare: props.compare, time: new Date().toUTCString(), isOpen: false };
    // console.log(props.compare);

    this.maxProfit = this.maxProfit.bind(this);
    this.click = this.click.bind(this);
  }

  componentDidMount() {
    this.interval = setInterval(() => this.setState({ time: new Date().toUTCString() }), 1000);
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  click() {
    this.setState({ isOpen: !this.state.isOpen });
  }

  maxProfit(arr, money) {
    let maxProfit = -1;
    let maxObj = null;
    let network = null;

    arr.forEach(element => {
      // console.log(element);
      if (element.Data.length > 0) {
        let val = element.Data[money];
        if (val.Profit !== undefined) {
          if (val.Profit > maxProfit) {
            maxProfit = val.Profit;
            maxObj = val;
            network = element.Networks[0];
          }
        }
      }
    });
    return {
      maxObj: maxObj,
      network: network
    };
  }

  render() {

    let oldticks = new Date(this.props.compare.UpdateTime).getTime();
    let newticks = new Date().getTime();
    let seconds = ((newticks - oldticks) / 1000).toFixed(0);

    let groups = this.props.compare.Groups;
    let profit = 0;
    let volume = 0;
    let spread = 0;
    let total = 0;
    if (groups.length > 0) {
      let groupMax = this.maxProfit(groups, this.props.money);
      if (groupMax.maxObj != null) {
        profit = groupMax.maxObj.Profit;
        volume = groupMax.maxObj.Volume;
        spread = groupMax.maxObj.Spread;
        total = (profit - (groupMax.maxObj.AskPrice * groupMax.network.WithdrawFee)).toFixed(2);
      }
    }

    let colorProfit = "text-success";

    if (total < 0) colorProfit = "text-danger";

    return (
      <>
        <Tr data-bs-toggle="collapse" href={"#collapse" + this.state.compare.Name} role="button" aria-expanded="false" aria-controls="collapseExample">
          <Td className="text-success">-{this.state.compare.Asset}-</Td>
          <Td>{spread}</Td>
          <Td>{volume}</Td>
          <Td className={colorProfit}>{profit} ({total})</Td>
          <Td key={seconds}>{seconds} s</Td>
        </Tr>
        <Tr className="collapse" id={"collapse" + this.state.compare.Name}>
          <Td colSpan="5">
            <div className='d-flex justify-content-end'>
              <button className='btn btn-outline-secondary' onClick={this.click}>Details</button>
            </div>
            <div className="d-flex flex-wrap justify-content-center">
              {groups.map((group, index) =>
                <SymbolVer2 key={group.Exchange1 + group.Exchange2 + group.Name1 + group.Name2} group={group} money={this.props.money} symbolName={this.state.compare.Name} profit={profit} volume={volume} spread={spread} />
              )}
            </div>
            {this.state.isOpen ? (
              <div className="d-flex flex-wrap justify-content-center">
                {this.props.compare.Symbols.map((symbol, index) =>
                  <SymbolVer3 key={symbol.Exchange + symbol.Name} symbol={symbol} isConnection={this.props.compare.IsConnection} />
                )}
              </div>
            ) : (<></>)}
          </Td>
        </Tr>
      </>
    );
  }

}
