import React, { Component } from 'react';
import { Networks } from "../components/Networks";
import { AddSymbolInfo } from './AddSymbolInfo';

export class SymbolVer3 extends Component {

    constructor(props) {
        super(props);
        this.state = { symbol: props.symbol, isConnection: props.isConnection };

        this.click = this.click.bind(this);
    }

    click() {
        this.setState({ isOpen: !this.state.isOpen });
    }

    render() {
        let isNetworks = false;
        if (this.props.symbol.AssetInfo != null && this.props.symbol.AssetInfo.Networks !== null) isNetworks = true;
        return (
            <div className="d-flex flex-column m-3 p-3 bg-gradient shadow border text-start">
                {this.props.symbol.Info ? (
                    <div className='d-flex w-full justify-content-end'>
                        <div className='position-absolute'>
                            <div className='d-flex flex-column'>
                                <div className='d-flex justify-content-end'><button className='btn btn-outline-secondary btn-sm' onClick={this.click}>Info</button></div>
                                {this.state.isOpen ? (
                                    <div className='d-flex flex-wrap border rounded-1 p-1 mt-1 bg-secondary text-wrap' style={{ width: "120px" }}>
                                        {this.props.symbol.Info}
                                    </div>
                                ) : (<></>)}
                            </div>
                        </div>
                    </div>
                ) : (<></>)}
                <a className='link-info text-reset' href={this.state.symbol.Link} target="_blank" rel="noopener noreferrer">{this.state.symbol.Exchange}</a>
                <div className='text-danger'>{this.props.symbol.BestAskPrice}</div>
                <div className='text-success'>{this.props.symbol.BestBidPrice}</div>
                {this.state.symbol.Hedging ? (<a className='link-warning' href={this.state.symbol.Hedging} target="_blank" rel="noopener noreferrer">hedging</a>) : (<></>)}
                <div className='text-white'>{new Date(this.props.symbol.DateTime).toLocaleString()}</div>
                <AddSymbolInfo asset={this.state.symbol.Asset} exchange={this.state.symbol.Exchange}/>
                {isNetworks ? (<><Networks networks={this.state.symbol.AssetInfo.Networks} symbol={this.state.symbol.Name} exchange={this.state.symbol.Exchange} price={this.state.symbol.BestAskPrice} /></>) : (<></>)}
            </div>
        );
    }

}