import React, { Component } from 'react';
import { NetworksVer2 } from "../components/NetworksVer2";

export class SymbolVer2 extends Component {

  constructor(props) {
    super(props);
    this.state = { group: props.group, symbolName: props.symbolName };
  }

  render() {
    return (
      <div className='d-flex flex-column m-3 p-3 bg-gradient shadow'>
        <div className='d-flex flex-column justify-content-center'>
            <div className='d-flex justify-content-center'>
              {this.state.group.Name1}
              &nbsp;➜&nbsp;
              {this.state.group.Name2}
            </div>
            <div className='d-flex justify-content-center'>
              <a className='link-info text-reset' href={this.state.group.Link1}  target="_blank" rel="noopener noreferrer">{this.state.group.Exchange1}</a>
              &nbsp;➜&nbsp;
              <a className='link-info text-reset' href={this.state.group.Link2}  target="_blank" rel="noopener noreferrer">{this.state.group.Exchange2}</a>
            </div>
            
            <div className='text-white'>Price: {new Date(this.props.group.DateTime1).toLocaleTimeString()} ➜ {new Date(this.props.group.DateTime2).toLocaleTimeString()}</div>
            <div className='text-white'>Book: {new Date(this.props.group.DateTimeOrderBook1).toLocaleTimeString()} ➜ {new Date(this.props.group.DateTimeOrderBook2).toLocaleTimeString()}</div>
            {this.state.group.Hedging?(<a className='link-warning' href={this.state.group.Hedging}  target="_blank" rel="noopener noreferrer">hedging</a>):(<></>)}
            {this.props.group.Data.length > 0 ?(<div className='d-flex flex-column justify-content-center'>
              <div>Spread: {this.props.group.Data[this.props.money].Spread} %</div>
              <div className='d-flex justify-content-center'>Volume: {this.props.group.Data[this.props.money].Volume} $ {this.props.group.Data[this.props.money].Volume !== this.props.group.Data[4].Volume?(<div className=' text-info'>&nbsp; 🡹 {this.props.group.Data[4].Volume} $</div>):(<></>)}</div>
              <div>Profit: {this.props.group.Data[this.props.money].Profit} $</div>
            </div>):(<></>)}
        </div>
        {this.props.group.Data.length > 0 ? (<><NetworksVer2 networks={this.state.group.Networks} symbol={this.state.symbolName} exchange={this.state.group.Exchange1+this.state.group.Exchange2} price={this.props.group.Data[this.props.money].AskPrice}/></>):(<></>)}
      </div>
    );
  }

}