import React, { Component } from 'react'

export class AddSymbolInfo extends Component {

    constructor(props) {
        super(props);
        this.state = { text: "", exchange: props.exchange, asset: props.asset };
        this.click = this.click.bind(this);
        this.change = this.change.bind(this);
        this.add = this.add.bind(this);
        this.remove = this.remove.bind(this);
    }

    async click() {
        if (this.state.text === "") this.remove();
        else this.add(this.state.text);
    }

    async add(text) {
        let token = sessionStorage.getItem("accessToken");
        let data = {
            method: 'POST',
            headers: {
                "Accept": "application/json",
                'Content-Type': 'application/json',
                "Authorization": "Bearer " + token, 
            },
            body: JSON.stringify({
                "id": this.state.asset + this.state.exchange,
                "text": text,
            })
        };
        let response = await fetch(`https://${process.env.REACT_APP_API}/Spread/addSymbolInfo`, data);
        let result = await response.json();
        if (response.ok) {
            this.setState({ text: "" });
        }
        else {
            alert(result);
        }
    }

    async remove() {
        let token = sessionStorage.getItem("accessToken");
        let data = {
            method: 'DELETE',
            headers: {
                "Accept": "application/json",
                "Authorization": "Bearer " + token, 
            }
        };
        await fetch(`https://${process.env.REACT_APP_API}/spread/removeSymbolInfo?id=` + this.state.asset + this.state.exchange, data);
    }

    change(e) {
        this.setState({ text: e.target.value })
    }

    render() {
        return (
            <div className="input-group input-group-sm" style={{ width: "150px" }}>
                <input type="text" className="form-control form-control-sm" placeholder="info" onChange={this.change} value={this.state.text} />
                <button className="btn btn-outline-secondary btn-sm" onClick={this.click}>Send</button>
            </div>
        );
    }

}