import React, { Component } from 'react';

export class Login extends Component {

  constructor(props) {
    super(props);
    this.state = { email: "", password: "", confirmPassword: "" }
    this.login = this.login.bind(this);
    this.registration = this.registration.bind(this);
    this.changeEmail = this.changeEmail.bind(this);
    this.changePassword = this.changePassword.bind(this);
    this.changeConfirmPassword = this.changeConfirmPassword.bind(this);
  }

  async login(){
    let data = {
      method: 'POST',
      headers: { 
        "Accept": "application/json",
        'Content-Type': 'application/json' 
      },
      body: JSON.stringify({
        email: this.state.email,
        password: this.state.password
        })
      };
    let response = await fetch(`https://${process.env.REACT_APP_API}/authentication/login`, data);
    if(response.status === 200){
      let result = await response.json();
      sessionStorage.setItem("accessToken", result.token);
      sessionStorage.setItem("isLogin", "true");
      this.setState({email: "", password: "", confirmPassword: ""});
      window.location.href = "/";
    }
    else{
      let result = await response.json();
      alert(result);
    }
  }

  async registration(){
    if(this.state.password === this.state.confirmPassword){
      let data = {
        method: 'POST',
        headers: { 
        "Accept": "application/json",
        'Content-Type': 'application/json' 
      },
        body: JSON.stringify({
          email: this.state.email,
          password: this.state.password
          })
        };
      let response = await fetch(`https://${process.env.REACT_APP_API}/authentication/registration`, data);
      if(response.status === 200){
        let result = await response.json();
        sessionStorage.setItem("accessToken", result.token);
        sessionStorage.setItem("isLogin", "true");
        this.setState({email: "", password: "", confirmPassword: ""});
        window.location.href = "/";
      }
      else{
        let result = await response.json();
        alert(result);
      }
    }
    else alert("Password mismatch!");
  }

  
  changeEmail(event) {
    this.setState({email: event.target.value.toLowerCase()});
  }
  
  changePassword(event) {
    this.setState({password: event.target.value});
  }

  changeConfirmPassword(event) {
    this.setState({confirmPassword: event.target.value});
  }

  render() {
    return (
      <div className='d-flex h-100 justify-content-center align-items-center'>
        <div className='border border-secondary bg-gradient login1'>
            <div className='border border-secondary bg-gradient login2'>
                <div className='border border-secondary bg-dark login3'>
                    <div className='p-5'>
                        <ul className="nav nav-pills navbar-dark justify-content-center m-2" id="myTab" role="tablist">
                            <li className="nav-item" role="presentation">
                                <button className="btn btn-dark mx-2 active" id="home-tab" data-bs-toggle="tab" data-bs-target="#home" type="button" role="tab" aria-controls="home" aria-selected="true">Log In</button>
                            </li>
                            <li className="nav-item" role="presentation">
                                <button className="btn btn-dark mx-2" id="profile-tab" data-bs-toggle="tab" data-bs-target="#profile" type="button" role="tab" aria-controls="profile" aria-selected="false">Register</button>
                            </li>
                        </ul>
                        <div className="h-75 tab-content" id="myTabContent">
                            <div className="tab-pane fade show text-white active" id="home" role="tabpanel" aria-labelledby="home-tab">
                                <div className='d-flex flex-column justify-content-center align-items-center'>
                                    <div className='m-2'>
                                        <input type="email" className="form-control" placeholder="Email" value={this.state.email} onChange={this.changeEmail}/>
                                    </div>
                                    <div className='m-2'>
                                        <input type="password" className="form-control" placeholder="Password" value={this.state.password} onChange={this.changePassword}/>
                                    </div>
                                    <button className="btn btn-outline-secondary px-5 m-2" onClick={this.login}>Next</button>
                                </div>
                            </div>
                            <div className="tab-pane fade" id="profile" role="tabpanel" aria-labelledby="profile-tab">
                                <div className='d-flex flex-column justify-content-center align-items-center'>
                                    <div className='m-2'>
                                        <input type="email" className="form-control" placeholder="Email" value={this.state.email} onChange={this.changeEmail}/>
                                    </div>
                                    <div className='m-2'>
                                        <input type="password" className="form-control" placeholder="Password" value={this.state.password} onChange={this.changePassword}/>
                                    </div>
                                    <div className='m-2'>
                                        <input type="password" className="form-control" placeholder="Password" value={this.state.confirmPassword} onChange={this.changeConfirmPassword}/>
                                    </div>
                                    <button className="btn btn-outline-secondary px-5 m-2" onClick={this.registration}>Next</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
      </div>
    );
  }
}
