import { useEffect, useState, useRef } from "react";
import "./MultiSelectComponent.css";

let load = null;
export function MultiSelectComponent(props) {
  const list = [
    { value: "M", label: "Hello" },
    { value: "F", label: "Hello2" },
    { value: "O", label: "Hello3" }
  ];
  const [selected, updateSelected] = useState([]);
  // console.log(selected);

  let listSelected = [];
  let listOptions = [];
  let opacity = 1;
  if(!props.disabled) {
    opacity = 0.4;
  }
  else{
    
    if(selected.length > 0 && props.list.length > 0) {
      let arr = selected.filter(e=>props.list.find((k) => k.value === e) !== undefined);
      if(arr.length !== selected.length) updateSelected(arr);
    }


    if(load === null && selected.length >= 0){
      let item = sessionStorage.getItem("networks");
      if(item !== null && JSON.parse(item).length > 0){
        let arr = JSON.parse(item);
        arr = arr.filter(f=>props.list.find(s=>s.value === f) !== null);
        updateSelected(arr);
        load = "load";
      }
      else if(props.list !== null && props.list.length > 0 && selected.length === 0) {
        load = "load";
        updateSelected(props.list.map(n=>n.value));
      }
    }

    if(selected.length >= 0){
      sessionStorage.setItem("networks", JSON.stringify(selected));
    }

    listSelected = selected;
    listOptions = props.list;
  }

  return (
    <div style={{zIndex:'100000000', opacity: opacity}} className="m-3">
      <MultiSelect
        selected={listSelected}
        options={listOptions}
        onChange={updateSelected}
        valuePresentation={false}
        placeholder={"Select..."}
        placeholderSelectedAll={"Selected all"}
      />
    </div>
  );
}

function MultiSelect({
  selected,
  options,
  onChange,
  valuePresentation,
  placeholder,
  placeholderSelectedAll,
  ...props
}) {
  const [listVisible, setListVisible] = useState(false);
  const wrapperRef = useRef(null);
  useOutsideClick(wrapperRef, () => setListVisible(false));
  return (
    <div id="select" ref={wrapperRef}>
      <div
        className={listVisible ? "field active" : "field"}
        onClick={() => {
          setListVisible((prev) => !prev);
        }}
        title={selected.join(",")}
      >
        {valuePresentation
          ? selected.length === 0
            ? placeholder
            : selected.length === options.length
            ? placeholderSelectedAll
            : selected
                .map((e) => options.find((k) => k.value === e)?.label)
                .join(",")
          : selected.length === 0
          ? placeholder
          : selected.length > 1
          ? `${selected.length} selected - ${(options.length - selected.length)} not`
          : selected}
          
      </div>
      
      {listVisible ? (
        
        <div id="list">
          <div className="item-wrapper">
            <label className="item">
              <input
                type="checkbox"
                checked={selected.length === options.length ? true : false}
                onChange={(e) => {
                  onChange((prev) => {
                    let array = [...prev];
                    if (array.length === options.length) {
                      array = [];
                    } else {
                      array = [...options.map((item) => item.value)];
                    }
                    return array;
                  });
                }}
                value={"Select all"}
              />
              <span className="marker"></span>
              {"Select all"}
            </label>
          </div>
          {options.map((item, i) => {
            return (
              <div key={i} className="item-wrapper">
                <label className="item">
                  <input
                    type="checkbox"
                    checked={selected.indexOf(item.value) !== -1 ? true : false}
                    onChange={(e) => {
                      onChange((prev) => {
                        let array = [...prev];
                        if (e.target.checked) {
                          array.push(e.target.value);
                        } else {
                          let index = array.indexOf(e.target.value);
                          if (index !== -1) {
                            array.splice(index, 1);
                          }
                        }
                        return array;
                      });
                    }}
                    value={item.value}
                  />
                  <span className="marker"></span>
                  {item.label}
                </label>
              </div>
            );
          })}
        </div>
      ) : null}
    </div>
  );
}

function useOutsideClick(ref, callback) {
  useEffect(() => {
    function handleClickOutside(event) {
      if (ref.current && !ref.current.contains(event.target)) {
        callback();
      }
    }
    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [ref, callback]);
}
