import React, { Component } from 'react';

export class NetworksVer2 extends Component {

  constructor(props) {
    super(props);
    this.state = { networks: props.networks, symbol: props.symbol, exchange: props.exchange, price: props.price };
  }

  render() {
    return (
        <div>
            <ul className="nav nav-pills navbar-dark justify-content-center m-2" id="myTabNetworks" role="tablist">
                {this.state.networks.map((network, index)=>
                    <li key={this.state.exchange + "-" + this.state.symbol + index} className="nav-item" role="presentation">
                        <button className="btn btn-dark mx-2" id={this.state.exchange + "-" + this.state.symbol + index + "-" + "tab"} data-bs-toggle="tab" data-bs-target={"#"+ this.state.exchange + "-" + this.state.symbol + index} type="button" role="tab">{network.Name}</button>
                    </li>
                )}
            </ul>
            <div className="h-75 tab-content" id="myTabContentNetworks">
                {this.state.networks.map((network, index)=>
                    <div key={this.state.exchange + "-" + this.state.symbol + index} className="tab-pane fade text-white" id={this.state.exchange + "-" + this.state.symbol + index} role="tabpanel">
                        <div className='d-flex flex-column align-items-center'>
                            <div>{"Fee: " + (network.WithdrawFee * this.props.price).toFixed(2) + " $"}</div>
                            <div>{"Min: " + (network.WithdrawMin * this.props.price).toFixed(2) + " $"}</div>
                            <div>{"Confirms: " + network.Confirmations}</div>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
  }

}