import React, { Component } from 'react';
import { Symbol } from "./Symbol";
import { Tr, Td } from 'react-super-responsive-table';
import 'react-super-responsive-table/dist/SuperResponsiveTableStyle.css';


export class Compare extends Component {

  constructor(props) {
    super(props);
    this.state = { compare: props.compare, time: new Date().toUTCString() };
    // console.log(props.compare);
  }

  componentDidMount() {
    this.interval = setInterval(() => this.setState({ time: new Date().toUTCString() }), 1000);
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  render() {
    
    let buy = this.props.compare.Symbols.find(symbol => symbol.IsBuy === true);
    let sell = this.props.compare.Symbols.find(symbol => symbol.IsBuy === false);
    
    let buyTicks = new Date(buy.DateTime).getTime();
    let sellTicks = new Date(sell.DateTime).getTime();

    // var oldDate = this.props.compare.UpdateTime.toString();
    // let ticks = Date.parse(this.state.time) - Date.parse(oldDate);
    // let updateTime = new Date(ticks).toLocaleTimeString([], { minute: "2-digit", second: "2-digit" });

    // let oldticks = new Date(this.props.compare.UpdateTime).getTime();
    let oldticks = buyTicks < sellTicks ? new Date(buy.DateTime).getTime() : new Date(sell.DateTime).getTime();
    let newticks = new Date().getTime();
    let seconds = ((newticks - oldticks) / 1000).toFixed(0);

    let color = "text-warning";
    let isConnection = this.props.compare.IsConnection;

    let symbols = this.props.compare.Symbols;


    if(isConnection === true) color = "text-success";
    else if(isConnection === false) color = "text-danger";

    return (
      <>
        <Tr data-bs-toggle="collapse" href={"#collapse" + this.state.compare.Name} role="button" aria-expanded="false" aria-controls="collapseExample">
          <Td className={color}>-{this.state.compare.Asset}-</Td>
          <Td>{this.props.compare.Spread.toFixed(2)}</Td>
          <Td>{buy.Exchange}</Td>
          <Td>{sell.Exchange}</Td>
          <Td key={seconds}>{seconds} s</Td>
        </Tr>
        <Tr className="collapse" id={"collapse" + this.state.compare.Name}>
          <Td colSpan ="6">
            <div className="d-flex flex-wrap justify-content-center">
              {symbols.map((symbol, index)=>
                <Symbol key={symbol.Exchange + "-" + symbol.Name} symbol={symbol} isConnection={isConnection}/>
              )}
            </div>
          </Td>
        </Tr>
      </>
    );
  }

}
